import React ,{ Component } from 'react';
import Todos from './deeptodo';
import AddTodo from './Addtodo';
class App extends Component{
  state={
    todos:[
      {id:1,content:"let's have fun with react-to-do list"}
    ]
  }
  
  deleteTodo=(id)=>{
    const todos=this.state.todos.filter(todo=>{ 
      return todo.id!== id;
      
    });
    this.setState({todos:todos})

  }
  addTodo=(todo)=>{
    todo.id=Math.random();
   
    const todos=[...this.state.todos,todo];
    this.setState({todos:todos})
    console.log(todo);
    
  }
  render(){
    console.log(this.state.todos);
    const honestyle={color: 'white',background: 'black',textAlign:'center'};
    const todostyle={background: 'blue',color: 'white',paddingLeft:'25%'};
   
    
  return(
    <div className="app" style={{background:'cyan',minHeight:'100vh',textAlign:'center'}}>
      <h1 style={honestyle}>Deep Shah's todo </h1>
      
      <Todos deleteTodo={this.deleteTodo} todos={this.state.todos} style={todostyle}/>
      <AddTodo addTodo={this.addTodo}/>
    </div>
  )
  }
}
export default App;